import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Image from "../components/image"
import "../css/components/header.scss"

const Header = ({ siteTitle, darkTheme = true, glitched }) => (
  <header
    className={
      "page " + (glitched ? "glitch " : " ") + (darkTheme ? "dark" : "light")
    }
  >
    <div className="wrapper">
      <div className="left">
        <Link to="/">
          <Image filename={"logos/vitamin_games_color_logo.png"} />
          <p data-text="Vitamin Games">{siteTitle}</p>
        </Link>
      </div>
      <div className="right">
        <ul>
          <li>
            <Link activeClassName={"active"} to="/">
              Home
            </Link>
          </li>
          <li>
            <Link activeClassName={"active"} to="/games">
              Games
            </Link>
          </li>
          <li>
            <Link activeClassName={"active"} to="/press">
              Press
            </Link>
          </li>
          <li>
            <Link
              activeClassName={"active"}
              href="https://store.vitamin.games"
              target="_blank"
            >
              Merch
            </Link>
          </li>
          <li>
            <Link
              activeClassName={"active"}
              href="https://discord.gg/projectarrhythmia"
              target="_blank"
            >
              Discord
            </Link>
          </li>
          <li>
            <Link activeClassName={"active"} to="/team">
              Team
            </Link>
          </li>
        </ul>
      </div>
    </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
  staticTheme: null,
}

export default Header
