import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Image from "../components/image"
import "../css/components/footer.scss"

const Footer = ({ darkTheme = true, glitched }) => (
  <footer
    className={
      "page " + (glitched ? "glitch " : " ") + (darkTheme ? "dark" : "light")
    }
  >
    <div className="wrapper">
      <div className="left">
        <Link to="/">
          <Image filename={"logos/vitamin_games_color_logo.png"} />
        </Link>
      </div>
      <div className="right">
        <p>
          Healthy Fun <span>(◕‿◕✿)</span>
        </p>
      </div>
    </div>
  </footer>
)

Footer.propTypes = {
  siteTitle: PropTypes.string,
}

Footer.defaultProps = {
  siteTitle: ``,
  staticTheme: null,
}

export default Footer
